import React from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router";

import { StepDocumentsValues, UpdateFormValuesFunction } from "../../../../types";
import { ControlledDatePicker } from "../../../../components/controlledDatePicker/ControlledDatePicker";
import { ControlledInput } from "../../../../components/controlledInput/ControlledInput";
import NavigationButton from "../../../../components/navigationButton/NavigationButton";
import NavigationLink from "../../../../components/navigationLink/NavigationLink";
import SectionInvoiceAttachment from "../../../../components/sectionInvoiceAttachment/SectionInvoiceAttachment";

import "./documents.scss";

interface DocumentsProps {
	formValues: StepDocumentsValues;
	updateFormValues: UpdateFormValuesFunction;
	updateFormStepsStatus: (action: "next" | "prev" | number) => void;
}

const Documents: React.FC<DocumentsProps> = ({ formValues, updateFormValues, updateFormStepsStatus }) => {
	const { handleSubmit, control, setValue } = useForm<StepDocumentsValues>({
		defaultValues: formValues,
		mode: "onSubmit",
	});
	const navigate = useNavigate();

	const [attachedFiles, setAttachedFiles] = React.useState(formValues.invoice);

	const replaceAttachedFiles = (value: File[] | null) => {
		setAttachedFiles(value);
		setValue("invoice", value, { shouldValidate: true });
	};

	const removeAttachedFile = (index: number) => {
		if (attachedFiles) {
			const filteredFiles = attachedFiles.filter((_, i) => i !== index);

			setAttachedFiles(filteredFiles);
			setValue("invoice", filteredFiles, { shouldValidate: true });
		}
	};

	return (
		<form
			className="documents-form"
			onSubmit={handleSubmit((data) => {
				updateFormValues("documents", data);
				updateFormStepsStatus("next");
				navigate("/new-order/address");
			})}
		>
			<div className="documents-form__invoice">
				<SectionInvoiceAttachment
					control={control}
					name="invoice"
					id="input_invoice"
					attachedFiles={attachedFiles}
					replaceAttachedFiles={replaceAttachedFiles}
					removeAttachedFile={removeAttachedFile}
				/>
			</div>
			<div className="documents-form__row documents-form__row--2-columns">
				<div className="documents-form__column">
					<ControlledInput
						control={control}
						name="lastName"
						id="input_lastName"
						label=" ()"
						rules={{
							required: "    .",
							pattern: {
								value: /^[-][-']*$/gi,
								message: "     ",
							},
						}}
					/>
				</div>
				<div className="documents-form__column">
					<ControlledInput
						control={control}
						name="firstName"
						id="input_firstName"
						label="' ()"
						rules={{
							required: "    .",
							pattern: {
								value: /^[-][-']*$/gi,
								message: "     ",
							},
						}}
					/>
				</div>
			</div>
			<div className="documents-form__row documents-form__row--2-columns">
				<div className="documents-form__column">
					<ControlledInput
						control={control}
						name="patronymicName"
						id="input_patronymic-name"
						label="  ()"
						rules={{
							required: "    .",
							pattern: {
								value: /^[-][-']*$/gi,
								message: "     ",
							},
						}}
					/>
				</div>
				<div className="documents-form__column">
					<ControlledInput
						control={control}
						name="passport"
						id="input_passport"
						label="   "
						rules={{
							required: " .",
							pattern: {
								value: /[-]{2}\d{6}/gi,
								message: "      123456",
							},
							maxLength: {
								value: 8,
								message: "      123456",
							},
						}}
					/>
				</div>
			</div>
			<div className="documents-form__row documents-form__row--2-columns">
				<div className="documents-form__column">
					<ControlledDatePicker
						control={control}
						name="birthDate"
						id="input_birth-date"
						label=" "
						rules={{
							required: "    .",
						}}
					/>
				</div>
				<div className="documents-form__column">
					<ControlledDatePicker
						control={control}
						name="passportIssueDate"
						id="input_passport-issue-date"
						label="  "
						rules={{
							required: "    .",
						}}
					/>
				</div>
			</div>
			<div className="documents-form__row">
				<ControlledInput
					control={control}
					name="passportIssuedBy"
					id="input_passport-issued-by"
					label=" "
					rules={{
						required: "    .",
						pattern: {
							value: /^[-][-']*$/gi,
							message: "     ",
						},
					}}
				/>
			</div>
			<div className="documents-form__row">
				<ControlledInput
					control={control}
					name="registrationAddress"
					id="input_registration-address"
					label=" "
					rules={{
						required: "    .",
						pattern: {
							value: /^[-][-']*$/gi,
							message: "     ",
						},
					}}
				/>
			</div>
			<div className="documents-form__row">
				<ControlledInput
					control={control}
					name="identificationNumber"
					id="input_identification-number"
					label=" "
					rules={{
						required: "    .",
						pattern: {
							value: /\d{10}/gi,
							message: "    10 ",
						},
						maxLength: {
							value: 10,
							message: "    10 ",
						},
					}}
				/>
			</div>
			<div className="documents-form__row documents-form__row--controls">
				<NavigationLink
					title=""
					to="/new-order/generalInformation"
					onClick={() => updateFormStepsStatus("prev")}
				/>
				<NavigationButton title="" iconPosition="right" type="submit" />
			</div>
		</form>
	);
};

export default Documents;
